}

.three-line::before {
  content: "";
  background-color: #fff;
  width: 3.5rem;
  height: 0.3rem;
  position: absolute;
  top: -0.7rem;
  left: 0.05rem;
}
@media only screen and (max-width: 43.75em) {
  .three-line::before {
    left: 0.027rem;
  }
}

.three-line::after {
  content: "";
  background-color: #fff;
  width: 3.5rem;
  height: 0.3rem;
  top: 0.7rem;
  position: absolute;
  left: 0.05rem;
}
@media only screen and (max-width: 43.75em) {
  .three-line::after {
    left: 0.025rem;
  }
}

.three-line-edit {
  width: 3rem;
  height: 0.3rem;
  background-color: black;
  position: absolute;
  top: 9%;
  right: 81%;
  transform: rotate(140deg);
  transition: all 1s;
  cursor: pointer;
}

.three-line-edit::before {
  content: "";
  background-color: #000;
  width: 4rem;
  height: 0.3rem;
  position: absolute;
  top: -0.8rem;
  left: 0.05rem;
  transform: rotate(138deg);
  visibility: hidden;
}

.three-line-edit::after {
  content: "";
  background-color: #000;
  width: 3rem;
  height: 0.3rem;
  top: 0.04rem;
  position: absolute;
  left: 0.05rem;
  transform: rotate(-102deg);
}

